.verbs {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.checkboxesWrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
}

.checkboxes {
    display: flex;
    border: 2px solid #2E3131;
    box-sizing: border-box;
    border-radius: 2px;
    height: 30px;
    justify-content: stretch;
}

.checkboxes label {
    width: 90px;
}

.checkboxes label:not(:first-child) {
    border-left: 2px solid #2E3131;
}

.checkboxes input {
    display: none;
}

.checkbox {
    user-select: none;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-in-out .3s;
    cursor: pointer;
}

.checkboxes input:checked + .checkbox {
    background-color: #2E3131;
    color: #F5F5F5;
}

.listsWrapper {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
}


.list {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 800px;
}

.list li {
    font-size: 20px;
    cursor: pointer;
    width: calc(100% / 3);
    list-style-position: inside
}

