.navigationBar {
    /*position: fixed;*/
    width: 100%;
    position: absolute;
    /*transition: allMovies ease-in-out .5s .5s;*/
    -webkit-transition: all ease-in-out .3s;
    transition: all ease-in-out .3s;
    overflow: hidden;
}

.navigationBar ul {
    margin: 8px;
    background-color: #2E3131;
    display: flex;
    height: 60px;
    justify-content: center;
    align-items: center;
}

.navigationBar li {
    min-width: 60px;
    margin: 0 16px;
    list-style: none;
}
.navigationBar a {
    color: #F5F5F5;
    text-decoration: none;
    font-size: 20px;
}
